//navigation 
.container {
    &__navigation {
        margin: 0 0 0 189px; //0 0 0 176px
       //медиа-запрос для 4k экранов
       //также позволяет в шапке избежать переход логотипа, меню и кнопки на
       //новую строку при зуме 33% , 25% (Ctrl -) в браузерах Chrome, Opera.  
        @media (min-width: $bp-4k ) { 
            margin: 0 0 0 25px;
            vertical-align: top; //added up
        } 
        
        //медиа-запрос нeтбуки
        @media (max-width: $bp-netbook ) { margin: 0 0 0 7%;}
    
    }    
}
//added up
//Хак для Firefox
//Браузер Firefox немного по другому отображает текст
//и чтобы верстка не разваливалась пришлось немного "подвинутся"
//Для этого применяю хак.
@-moz-document url-prefix() {
    .container {
        &__navigation { margin: 0 0 0 184px;}
    }
}
.navigation {display: inline-block}

//делаю вложенность что бы повысить значимость тем самым
//позволяю повлиять медиа-запросу сразу на два класса
//тем самым добиваюсь применения стилей в нужной последовательности
//также ухожу от ошибки при компиляции sass  которая возникает
//если медиа запрос записать  в вправило к .navigation
//а если в .container__navigation записать медиа-запрос
//то правила в запросе не переопределяют правило в .navigation
//то есть display: inline-block так и остается
//поэтому было принято такое решение: 
.navigation.container__navigation{
    //медиа-запрос планшет
    @media (max-width: $bp-tablet ) {
        display: block;
        margin: 0;
        text-align: center;
    }
 }

 //added up
 //У меня есть небольшое сожаления что я поступил именно так
 //но всё же это решения меня на сегодня устраивает.
 //Браузер Яндекс он при переводе сайта на русский не добавляет
 //никаких сторонних классов мне пришлось сделать селектор по атрибуту
 //и это работает.Теперь кнопка в Яндекс браузере не падает.
 //Также это работает и в других браузерах которые переведут страницу на русский
 //root это html
 .root[lang=ru]{
    .navigation.container__navigation{
        margin: 0 0 0 46px;
    }
    @media (max-width: $bp-netbook ) {
        .navigation.container__navigation{
            margin: 0;
        }
    }
 }
 
